import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography
} from "@mui/material";

export default function BasicTextFields() {
  return (
    <div style={{ display: "flex" }}>
      <Card sx={{ maxWidth: 1920 }}>
        <CardMedia
          component="img"
          height="500"
          image="ongaku.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h7" component="div">
            音楽を聴く事によって得られる効果
          </Typography>
          <Typography variant="body2" color="text.secondary">
            音楽を聴いていると、自分で意識していなくても心や体に大きな変化が起きるもの。まず音楽が大きな影響を与えるのが身体です。音楽を聴いているうちに、知らず知らずのうちに身体がリズムに合わせて動いていたり、足や指でリズムを取ったりしているもの。
            また、ランニングやウォーキングをするとき、ちょっと気分が乗らない、やる気が出ないというときでも、音楽を聴いているうちに身体が快調に動き始めるということもあります。心に及ぼす影響は、たとえば、心地よい音楽を聴いていると心が落ち着く、いらい
            らしているときでも好きな音楽を聴いたり、歌ったりするとリラックスできることもある。その他にも、落ち込んだ気持ちのときに音楽を聴いて元気になったり、リズミカルな音楽を聴いてやる気を出したり、普段から音楽を
            毎日の生活に利用している人も多い。
            さらに音楽を聴くことで、昔懐かしいことを思い出すといったことも珍しいものではない。このように音楽を聴くことで、得られるメリットが多い。
          </Typography>
        </CardContent>
      </Card>
    </div>
  );
}
