import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography
} from "@mui/material";

export default function BasicTextFields() {
  return (
    <div style={{ display: "flex" }}>
      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="450"
          image="shinderera.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            4位
          </Typography>
          <Typography gutterBottom variant="h7" component="div">
            シンデレラボーイ
          </Typography>
          <Typography variant="body2" color="text.secondary">
            恋愛を美化せず、辛さや儚さをリアルに感じさせた曲。たたみかけるようにハイトーンが続き、音の上下が激しいので聴いていても歌っていても楽しい曲。
          </Typography>
        </CardContent>
      </Card>

      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="450"
          image="marigorudo.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            5位
          </Typography>
          <Typography gutterBottom variant="h7" component="div">
            マリーゴールド　
          </Typography>
          <Typography variant="body2" color="text.secondary">
            あいみょんを一躍有名にした曲。何処か懐かしさのある曲調、覚えやすいメロディー、覚えやすい歌詞で男性からも女性からも人気のある名曲。
          </Typography>
        </CardContent>
      </Card>
    </div>
  );
}
